//  -    JS - SwiperLimited.js

import React, { useRef, useState } from "react";
import $ from 'jquery';
import { easeOutExpo } from "jquery-ui";
import { Swiper, SwiperSlide } from "swiper/react";
import { Virtual } from 'swiper';
import {Link} from 'react-router-dom';

// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
import "./swiperLimited.css";
import Details from "../modules/Details";
import limited_product_data from "../../data/limitedProduct";




//////////////////////////// [     ] ////////////////////////////
const slideUpAni = ()=>{
    $(()=>{
        
    const pd_slide = $(".slide");
    const pd_tit = $(".product_tit");
    console.log("  : ", pd_slide);

    // [ ()    ]
    //   /      
    pd_slide.hover(
        // 
        function(){
            //     
            let hval = $(this).find(".product_tit").height();
            console.log("     : ", hval);

            //        
            $(this).siblings().find("img").stop().animate({
                opacity: "0.5"
            }, 400);

            //       
            $(this).find(".wrap").stop().animate({
                height: hval + "px",
                opacity: "1"
            }, 500, easeOutExpo);
        },
        // 
        function(){
            console.log(" ");

            //        
            $(this).siblings().find("img").stop().animate({
                opacity: "1"
            }, 400);

            //    0   
            $(this).find(".wrap").stop().animate({
                height: "0px",
                opacity: "0"
            }, 500, easeOutExpo);
        }
    );

    
});

} //////////////////////// slideUpAni  ///////////////////////////////


//     




// [   ]
export default function SwiperLimited() {
    // props.pgname -   ( )

    //   
    const selecData = limited_product_data;
    //  Hook
    const [dbseq,setDbseq] = useState(0);

    const showFn = (idx)=>{
        console.log("!",idx);
        setDbseq(idx);

    };
    
    return (
        <>
            <section className="swipe_banner limited">
                <Swiper
                
                    slidesPerView={1}
                    // "Limited"    30  0
                    spaceBetween={30}
                    centeredSlides={false}
                    breakpoints={{
                        200: {
                            slidesPerView: 1,
                        },
                        700: {
                            slidesPerView: 2,
                        },
                        1000: {
                            slidesPerView: 3,
                        },
                    }}
                    className="swiper-limited"
                >
                    <ul className="swiper-wrapper">
                        {
                            selecData.map((v, i)=>
                            <SwiperSlide className="swiper-slide slide limited" key={i} onClick={()=>showFn(i)}>
                                {/* {console.log(v.isrc)} */}
                                <div className="wrap">
                                    <span className="product_tit limited">{v["productName"]}</span>
                                </div>
                                <img src={v["isrc"]} alt=" " />
                            </SwiperSlide>
                            )
                        }
                    </ul>
                </Swiper>
            </section>
            <Details dbseq={dbseq} />
            {/* js    */}
            {/* {slideUpAni()} */}
        </>
    );
}



