//      JS - ScrollTop.js

import { useEffect } from "react";
import { useLocation } from "react-router-dom";

//  
const ScrollTop = ()=>{
    //    
    const {pathname} = useLocation();

    //   
    useEffect(()=>{
        //   
        window.scrollTo(0, 0);
    }, [pathname]); /////////////// useEffect() ///////////////

    // null :      
    return null;

}; /////////////////////// ScrollTop  ///////////////////////

// 
export default ScrollTop;